
header {
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
}

header .container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

header div {
  margin: 10px 0;
}

.header-left {
  background: #eee;
  padding: 20px;
}

.header-left form {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header-left .btn-danger {
  margin-left: 10px;
}

label {
  padding-right: 5px;
  -webkit-align-self: center;
          align-self: center;
  margin-bottom: 0;
}
.header-left form {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: inherit;
          align-items: inherit;
}
.cost-input,
.budget-input {
  width: 60px;
}

button.btn {
  border-radius: 0;
}

.budget-display {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.reset-budget-btn {
  margin-left: 5px;
}

input {
  border: none;
  border-bottom: 1px solid #ddd;
  margin: 0 10px;
  padding: 5px;
}

label { margin: 0; }

#expenses {
  position: relative;
}

.clear-all-expenses-btn {
  position: absolute;
  right: 0;
  top: 0;

}

.table { width: 100%; max-width: 600px; margin: 0 auto; }

.table td {
  padding: 0;
  vertical-align: middle;
}

td.expense-name,
td.expense-cost {
  padding: 0 5px;
}

td.expense-edit-btn { width: 60px }
td.expense-remove-btn { width: 70px; }

table .btn-danger,
table .btn-secondary {
  width: 100%;
}

.btn {
  border: 0; border-radius: 0;
  border: 0;
  border-radius: 0;
}

#empty-expense-list-msg {
  font-size: 1.4em;
  color: #920303;
  font-style: italic;
}

.error-msg,
.danger {
  color: #920303;
}
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Patrick Hand', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


